.gin--vertical-toolbar .sidebar {
  left: var(--ginVerticalToolbarOffset, 240px) !important;
}

.gin--vertical-toolbar .sidebar__menu {
  left: calc(128px + var(--ginVerticalToolbarOffset, 240px));
}

.gin--vertical-toolbar .header {
  left: var(--ginVerticalToolbarOffset, 240px) !important;
  width: calc(100% - var(--ginVerticalToolbarOffset, 240px)) !important;
}

.news-single__title {
  max-width: 80%;
}


.news-single__text  ul {
  margin-left: 64px;
}

.news-single__text  ul li {
  margin: 20px 0;
}
.news-single__text ul>li:before {
  content: "\2014\a0";
}
.news-single__text ul>li:before {
  float: left;
  margin-left: -32px;
}

.news-single__text a {
  text-decoration: none;
  color: #265FC1;
}

.webform-submission-form .form-type-webform-terms-of-service a {
  color: #255ec1;
}

.webform-submission-form input.form-checkbox:checked {
  background-color: none;
}

.webform-submission-form input.form-checkbox:checked:after {
  content: '';
  background-color: red;
  width: 10px;
  height: 15px;
  left: 8px;
  top: 7px;
  display: block;
  position: absolute;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
}

.news-single__text table td {
  border: 1px solid rgba(49,49,49,.2);
  padding: 20px
}

.footer a {
  color: #fff;
}
